@import url("https://fonts.googleapis.com/css2?family=Work+Sans:wght@300&display=swap");

.App{
  min-height: 100vh;
  display: flex;
  background-image: url("https://static.vecteezy.com/system/resources/previews/010/563/706/non_2x/chat-dialog-boxes-in-glass-morphism-effect-style-transparent-frosted-acrylic-speech-bubble-on-color-gradient-circles-realistic-glassmorphism-matte-plexiglass-message-shapes-vector.jpg");
  background-size: cover;
  background-position: center;
}

::-webkit-scrollbar {
  width: 0px;
}

::-webkit-scrollbar-thumb {
  background: rgba(136, 136, 136, 0.281);
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}